@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --font-primary: #fff;
  --font-secondary: #282828;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
  color: #fff;
  color: var(--font-primary);
  font-family: 'Montserrat', sans-serif;
}

body {
  height: 100vh;
}

@media (max-width: 1440px) {
  html{
    font-size: 50%;
  }
}

@media (max-width: 1024px) {
  html{
    font-size: 40%;
  }
}

@media (max-width: 768px) {
  html{
    font-size: 32%;
  }
}

  .sidebar-desktop {
    height    : 260px;
    width     : 100%;
    background: linear-gradient(180deg, rgba(8, 135, 212, 1) 0%, rgba(8, 135, 212, .5) 100%);
    position  : absolute;
    z-index   : -1;
    font-size : 2rem;
  }

  .sidebar-desktop>.page-selected {
    position        : relative;
    display         : flex;
    justify-content : center;
    align-items     : center;
    width           : 14rem;
    height          : 14rem;
    border-radius   : 50%;
    background-color: #fff;
    top             : 12%;
    left            : 72.5%;
  }

  .nav-bar {
    position: relative;
    z-index : 20;
    display : flex;
  }

  .nav-bar>a>img {
    position: relative;
    top     : 16%;
    left    : 6%;
    width   : 6rem;
    height  : 6rem;
  }

  .nav-bar>.nav-items>ul {
    display: flex;
  }

  .nav-bar>.nav-items>ul>li {
    display        : flex;
    justify-content: space-between;
    padding        : 8px;
    margin-right: 20px;
  }

  .nav-bar>.nav-items>ul>li>a {
    text-decoration: none;
    color          : var(--font-primary);
    text-align     : center;
    font-weight    : bold;
    padding-top    : 8px;
    padding-bottom : 8px;
    margin-top     : 8px;
    margin-bottom  : 8px;
    min-width      : 100px;
    min-height     : 42px;
    height         : 60px;
  }

  .nav-bar>.nav-items>ul>li>a:hover {
    border-bottom: 2px solid #fff;
  }

  /* Style The Dropdown Button */
  .dropbtn {
    color           : #fff;
    padding-top     : 8px;
    padding-bottom  : 8px;
    margin-top      : 18px;
    margin-bottom   : 8px;
    min-width      : 100px;
    min-height     : 42px;
    text-align      : center;
    font-size       : 2rem;
    font-weight     : bold;
    border          : none;
    cursor          : pointer;
    background-color: transparent;
  }

  /* The container <div> - needed to position the dropdown content */
  .dropdown {
    position: relative;
    display : inline-block;
  }

  /* Dropdown Content (Hidden by Default) */
  .dropdown-content {
    display         : none;
    position        : absolute;
    background-color: #f9f9f9;
    min-width       : 158px;
    border-radius   : 4px;
    box-shadow      : 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index         : 1;
    transition      : all .3s;
  }

  /* Links inside the dropdown */
  .dropdown-content a {
    color          : black;
    font-size      : 1.8rem;
    padding        : 12px 16px;
    text-decoration: none;
    display        : block;
  }

  /* Change color of dropdown links on hover */
  .dropdown-content a:hover {
    background-color: #f1f1f1
  }

  /* Show the dropdown menu on hover */
  .dropdown:hover .dropdown-content {
    display: block;
  }

  /* Change the background color of the dropdown button when the dropdown content is shown */
  .dropdown:hover .dropbtn {
    background-color: transparent;
  }

  @media (max-width: 1440px) {
    .sidebar-desktop>.page-selected {
      left: 77%;
    }
  }
  @media (max-width: 1024px) {
    .sidebar-desktop>.page-selected {
      display: none;
    }
  }
  @media (max-width: 768px) {
    .sidebar-desktop {
      display: none;
    }
  }
